<template>
  <list
  :list="list"
  :total="total"
  :loading="loading"
  @click="toPage"
  @search="toSearch"
  />
</template>

<script>
import debounce from '@/tools/debounce';
import formatDate from '@/tools/format-date';
import config from '@/config';

export default {
  data() {
    return {
      results: [],
      total: 0,
      page: 1,
      keyword: '',
      loading: true,
    };
  },
  computed: {
    list() {
      return this.results.map(obj => ({
        content: `${obj.title} - ${obj.score}/${obj.totalScore} - ${formatDate(obj.createdAt)}`,
        // eslint-disable-next-line
        to: `/results/${obj._id}`,
      }));
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.page = page;
      this.getData();
    },
    toSearch(keyword) {
      this.keyword = keyword;
      this.getData();
    },
    getData: debounce(function () {
      const user = window.$user;
      if (user) {
        this.$axios.get(`
        ${this.$url.results}?username=${user.username}&page=${this.page}&keyword=${this.keyword}`)
          .then((resp) => {
            this.results = resp.data.results;
            this.total = resp.data.total;
            this.loading = false;
          })
          .catch(console.log);
      }
    }, config.debounceDelay),
  },
};
</script>
