<template>
  <list
  :list="list"
  :total="total"
  :loading="loading"
  @click="toPage"
  @search="toSearch"
  />
</template>

<script>
import debounce from '@/tools/debounce';
import formatDate from '@/tools/format-date';
import config from '@/config';

export default {
  data() {
    return {
      data: [],
      page: 1,
      total: 0,

      keyword: '',
      loading: true,
    };
  },
  computed: {
    list() {
      return this.data.map(obj => ({
        content: `${`${obj.content.substr(0, 12)}...`} - ${obj.title} - ${formatDate(obj.createdAt)}`,
        to: `/tests/${obj.title}`,
      }));
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.page = page;
      this.getData();
    },
    toSearch(keyword) {
      this.keyword = keyword;
      this.getData();
    },
    getData: debounce(function () {
      const user = window.$user;
      if (user) {
        this.$axios.get(`${this.$url.comments.users}/${user.username}?page=${this.page}&keyword=${this.keyword}`)
          .then((resp) => {
            this.data = resp.data.results;
            this.total = resp.data.total;
            this.loading = false;
          })
          .catch((err) => {
            console.log(err);
          });
      }
    }, config.debounceDelay),
  },
};
</script>
