<template>
  <div class="flex-column relative">
    <form class="bg-2 pd-1 flex-row pure-form" @submit.prevent="">
      <input
      type="text"
      placeholder="Search"
      class="flex-item-center"
      v-model="keyword"
      >
      <button class="ml-auto flex-item-center pure-button pure-button-primary">
        <router-link to="/workshop/tests">
          创建测试
        </router-link>
      </button>
    </form>
    <div class="pd-1 flex-1 flex-column border-box" v-loading="loading">
      <no-data v-if="tests.length === 0 && !loading">
      </no-data>
      <table class="pure-table table-fixed">
        <thead>
          <tr>
            <th>标题</th>
            <th>创建时间</th>
            <th>状态</th>
          </tr>
        </thead>
        <tbody>
          <tr 
          v-for="test in tests"
          :key="test.title"
          >
            <td class="w-70">
              <router-link :to="'/tests/'  + test.title">
                {{ test.title }}
              </router-link>
            </td>
            <td class="w-20">
              {{ test.createdAt | formatDateTime }}
            </td>
            <td class="w-10" 
            :class="{
              'text-success': test.settings.accessbility === 0, 
              'text-danger': test.settings.accessbility === 1, 
              }"
            >
              {{ test.settings.accessbility === 0 ? '公开' : '私有' }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <pager
    :total="total"
    @click="toPage"
    />
  </div>
</template>

<script>
import debounce from '@/tools/debounce';
import config from '@/config';

export default {
  data() {
    return {
      tests: [],

      page: 1,
      total: 0,

      keyword: '',
      loading: true,
    };
  },
  watch: {
    keyword() {
      this.getData();
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.page = page;
      this.getData();
    },
    getData: debounce(function () {
      if (window.$user) {
        this.$axios.get(`${this.$url.tests}?username=${window.$user.username}&page=${this.page}&keyword=${this.keyword}`)
          .then((resp) => {
            this.tests = resp.data.results;
            this.total = resp.data.total;
            this.loading = false;
          })
          .catch(console.log);
      }
    }, config.debounceDelay),
  },
};
</script>