<template>
  <div class="flex-row flex-1 profile" >
    <div class="profile-nav flex-column shadow-1">
      <div class="pd-1">
        <img class="profile-avatar" :src="user.avatar" alt="avatar">
        <div class="profile-meta">
          {{ user.username }}
        </div>
        <div class="profile-meta">
          {{ user.email }}
        </div>
        <p class="profile-meta">
          {{ user.bio }}
        </p>
      </div>
      <ul class="profile-tabs">
        <li
          class="profile-tabs__item pd-1 text-center"
          v-for="tab in tabs" :key="tab.component"
          :class="{'profile-tabs__item--active': active === tab.component}"
          @click="active = tab.component"
        >
          {{ tab.name }}
        </li>
      </ul>
    </div>
    <component
    class="flex-1"
    :is="active"
    >
    </component>
  </div>
</template>

<script>
import Activities from './Activities/index.vue';
import Tests from './Tests/index.vue';
import Results from './Results/index.vue';
import Comments from './Comments/index.vue';
import Settings from './Settings/index.vue';
import Cards from './Cards/index.vue';

export default {
  components: {
    Tests,
    Cards,
    Results,
    Comments,
    Activities,
    Settings,
  },
  props: {
    username: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      user: {},
      loading: true,

      active: 'Tests',
      tabs: [
        {
          name: '测试',
          component: 'Tests',
        },
        {
          name: '卡片',
          component: 'Cards',
        },
        {
          name: '测试记录',
          component: 'Results',
        },
        {
          name: '评论',
          component: 'Comments',
        },
        {
          name: '足迹',
          component: 'Activities',
        }
      ],
    };
  },

  mounted() {
    this.$axios.get(`${this.$url.users}/${this.username}`)
    .then((resp) => {
      this.user = resp.data;
      this.loading = false;
      if (window.$user && window.$user.username === this.username) {
        this.tabs.push({
          name: '设置',
          component: 'Settings',
        });
      }
    })
    .catch((err) => {
      this.alert(err.response.message);
    })
  },
};
</script>

