  width: 12em;
  color: #777;
  z-index: 1000;
  
  .profile-avatar {
    width: 10em;
    height: 10em;
    border-radius: 50%;
    object-fit: cover;
  }
  .profile-meta {
    margin-top: 0.4em;
    font-weight: bold;
    word-break: break-all;
  }
  .profile-tabs {
    list-style: none;
    padding: 0;
    margin: 0;
    .profile-tabs__item {
      line-height: 2em;
      &:hover {
        background: #ccc;
        cursor: pointer;
      }
    }
    .profile-tabs__item--active {
      background: #ccc;
    }
  }
}


</style>
