<template>
  <div class="flex-column flex-1 relative" v-loading="loading">
    <form class="bg-2 pd-1 flex-row pure-form" @submit.prevent="">
      <input
      type="text"
      placeholder="Search"
      class="flex-item-center"
      v-model="keyword"
      >
      <button class="ml-auto flex-item-center pure-button pure-button-primary">
        <router-link to="/workshop/cards">
          创建卡片
        </router-link>
      </button>
    </form>
    <div class="cards flex-1 pd-1">
      <card-group 
        v-for="(card, index) in cards"
        :key="card.title + index"
        @click="toCard(card)"
        :data="card"
      >
      </card-group>
    </div>
    <pager
    class="mt-auto ml-auto mr-auto"
    :total="total"
    @click="toPage"
    />
  </div>
</template>

<script>
import debounce from '@/tools/debounce';
import config from '@/config';

export default {
  data() {
    return {
      cards: [],

      page: 1,
      total: 0,

      keyword: '',
      loading: true,
    };
  },
  watch: {
    keyword() {
      this.getData();
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.page = page;
      this.getData();
    },
    toCard(card) {
      this.$router.push(`/cards/${card.title}`);
    },
    getData: debounce(function () {
      if (window.$user) {
        this.$axios.get(`${this.$url.cards}?username=${window.$user.username}&page=${this.page}&keyword=${this.keyword}`)
          .then((resp) => {
            this.cards = resp.data.results;
            this.total = resp.data.total;
            this.loading = false;
          })
          .catch(console.log);
      }
    }, config.debounceDelay),
  },
};
</script>

<style lang="scss" scoped>
$gap: 0.8em;
$width: 150px;
$height: 200px;
$repeat: 6;

.cards {
  display: grid;
  grid-gap: 0.8em;
  grid-template-columns: repeat($repeat, $width);
  grid-auto-rows: $height;
}
</style>

